import http from "./http";
import axios from "axios";
import { getJwt } from "./auth";
// import { toast } from "react-toastify";

/**--------------------------------------
 ** Build GET Api string
 * --------------------------------------
 * @param {string} category
 * @returns {object}
 */
export const getAPI = async (path, values) => {
  try {
    if (values) {
      return getJwt()
        .then(() => http.get(`get/${path}.php`, { params: values }))
        .catch((e) => {
          if (e.response.data.error.message === "Token is not valid!") {
            getJwt(true);
            window.location.reload();
          } else {
            console.log(e.response);
            // toast.error("Something went wrong!");
            return { data: { error: e } };
          }
        });
    } else
      return getJwt()
        .then(() => http.get(`get/${path}.php`))
        .catch((e) => {
          if (e.response.data.error.message === "Token is not valid!") {
            getJwt(true);
            window.location.reload();
          } else {
            console.log(e.response);
            // toast.error("Something went wrong!");
            return { data: { error: e } };
          }
        });
  } catch (e) {
    console.log(e);
    return {};
  }
};

/**----------------------------------
 ** Get all envelopes
 * --------------------------------------
 * @param {string} category
 * @returns {object}
 */
export const getAll = async (values) => {
  try {
    const data = await getAPI("get/index.php", {
      request: "all",
      owner: "tw-854334",
    });
    data && !data.success && !data.error && console.log(data);
    return data;
  } catch (e) {
    console.log(e);
    return {};
  }
};
